* {
  margin: 0;
  padding: 0;
}

body {
  margin: 50px;
}

header {
  margin-bottom: 50px;
}

h1, h2, h3 {
  font-family: Helvetica, sans-serif;
  font-size: 14pt;
  letter-spacing: -0.5pt;
}

h2 {
  margin-bottom: 20px;
}

p {
  font-family: "Inconsolata", monospace;
  font-size: 14pt;
  letter-spacing: -0.6pt;
}

a {
  text-decoration: none;
  color: blue;
}

a:hover {
  color:darkslategrey;
}